<template>
<bar-chart ref="BarChart" :chart-data="datacollection" :options="options"></bar-chart>
</template>

<script>
import BarChart from './BarChart.js'
import axios from 'axios'

export default {
  components: {
    BarChart
  },
  computed:{
    acumuladoDolares:{
      get(){
        if(this.aDolares.length>0){
          return "$" + Math.round(this.aDolares[this.aDolares.length-1]).toLocaleString()
        }
        return null
      }
    },
    acumuladoCetes:{
      get(){
        if(this.aCetes.length>0){
          console.log(this.aCetes.reduce((a, b) => a + b))
          return "$" + Math.round(this.aCetes.reduce((a, b) => a + b)).toLocaleString()
        }
        return null
      }
    }
  },
  data() {
    return {
      gradient: null,
      gradientCete: null,
      gradientDollar: null,
      valor: 0,
      plazo: 0,
      acumuladoSii: 0,
      rendimiento: 0,
      plusvalia: 0,
      arreglo: new Array(),
      aCetes: new Array(),
      aDolares: new Array(),
      prcCete: 0,
      labels: new Array(),
      datacollection: {},
      options: {
        scales: {
          yAxes: [{
            scaleLabel: {
              display: true,
              labelString: 'MXN'
            },
            ticks: {
              beginAtZero: true,
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [{
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: true
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  mounted() {
    this.gradient = this.$refs.BarChart.$refs.canvas.getContext('2d').createLinearGradient(0, 0, 0, 450)
    this.gradient.addColorStop(0, 'rgba(0, 102, 179,1)')
    this.gradient.addColorStop(.5, 'rgba(0, 115, 201,0.7)')
    this.gradient.addColorStop(1, 'rgba(0, 145, 255,.5)')

    this.gradientCete = this.$refs.BarChart.$refs.canvas.getContext('2d').createLinearGradient(0, 0, 0, 450)
    this.gradientCete.addColorStop(0, 'rgba(255, 79, 79,1)')
    this.gradientCete.addColorStop(.5, 'rgba(255, 79, 79,0.7)')
    this.gradientCete.addColorStop(1, 'rgba(255, 79, 79,.5)')

    this.gradientDollar = this.$refs.BarChart.$refs.canvas.getContext('2d').createLinearGradient(0, 0, 0, 450)
    this.gradientDollar.addColorStop(0, 'rgba(2, 133, 0,1)')
    this.gradientDollar.addColorStop(.5, 'rgba(2, 133, 0,0.7)')
    this.gradientDollar.addColorStop(1, 'rgba(2, 133, 0,.5)')
  },
  methods: {
    fillData() {
      this.datacollection = {
        labels: this.labels,
        datasets: [{
          label: 'SiiTrazo* *',
          backgroundColor: this.gradient,
          pointBackgroundColor: this.gradient,
          borderWidth: 1,
          borderColor: this.gradient,
          pointBorderColor: this.gradient,
          data: this.arreglo
        }, {
          label: 'Cetes* *',
          backgroundColor: this.gradientCete,
          pointBackgroundColor: this.gradientCete,
          borderWidth: 1,
          borderColor: this.gradientCete,
          pointBorderColor: this.gradientCete,
          data: this.aCetes
        }, {
          label: 'Dólares',
          backgroundColor: this.gradientDollar,
          pointBackgroundColor: this.gradientDollar,
          borderWidth: 1,
          borderColor: this.gradientDollar,
          pointBorderColor: this.gradientDollar,
          data: this.aDolares
        }]
      }
    },
    updateData(valor, plazo, rendimiento, plusvalia) {
      this.valor = valor
      this.plazo = plazo
      this.rendimiento = rendimiento
      this.plusvalia = plusvalia
      this.createData()
      this.fillData()
    },
    procesarDatos(datos, t) {
      var cierre = [];
      for (var i = 0; i < datos.length; i++) {
        cierre[i] = datos[i].dato;
      }
      if (t == 'd') {
        this.aDolares = this.monthlyValues(this.average(this.chunkify(cierre, this.plazo, true)))
      } else if (t == 'c') {
        this.aCetes=new Array()
        this.prcCete = datos[0].dato
        var prctCete = Number(this.prcCete) * .01 / 12
        var pC = this.valor * prctCete
        for (var j = 0; j < this.plazo; j++) {
          this.aCetes.push(pC);
        }
      }
      this.fillData()
    },
    createData() {
      this.labels = new Array()
      this.arreglo = new Array()
      //aqui se genera el porcentaje para multiplicar
      try {
        this.getCeteData().then(res =>
          this.procesarDatos(res, 'c')
        )
        this.getDollarData().then(res =>
          this.procesarDatos(res, 'd')
        )
      } catch (e) {
        console.log("No se pudo llamar al api")
      }
      var prct = (this.rendimiento + this.plusvalia) * .01 / 12 //supongo que en vez de este 12 habrá que poner this.plazo
      var p = Math.round(this.valor * prct)
      this.acumuladoSii = "$" + (Math.round(p * this.plazo)).toLocaleString()
      for (var i = 0; i < this.plazo; i++) {
        //aqui se hace la multiplicación de cada mes por el porcentaje para llenar la grafica
        this.arreglo.push(p);
        this.labels.push("Mes " + (i + 1))
      }
    },
    getFirstDate() {
      var today = new Date();
      var dd = String(today.getDate()).padStart(2, '0');
      var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
      var yyyy = today.getFullYear();
      var firstDate = yyyy + "-" + mm + "-" + dd;
      return firstDate
    },
    getSecondDate(days, months, years, b) {
      var date = new Date();
      date.setDate(date.getDate() + days);
      date.setMonth(date.getMonth() + months);
      date.setFullYear(date.getFullYear() + years);
      var dd = String(date.getDate()).padStart(2, '0');
      var mm = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
      var yyyy = date.getFullYear();
      if (b === true) {
        var secondDate = yyyy + "-" + mm + "-" + dd;
      } else {
        secondDate = yyyy + "-" + mm + "-" + "01";
      }
      return secondDate;
    },
    getDollarData() {
      return axios.get('https://www.banxico.org.mx/SieAPIRest/service/v1/series/SF43718/datos/' + this.getSecondDate(0, -this.plazo, 0, true) + "/" + this.getFirstDate() + '?token=478d9e956a835d3bd51f133a686556d79314083713b9ccafa2690b41475c5cc8')
        .then(response => response.data.bmx.series[0].datos)
    },
    getCeteData() {
      return axios.get('https://www.banxico.org.mx/SieAPIRest/service/v1/series/SF43936/datos/oportuno?token=478d9e956a835d3bd51f133a686556d79314083713b9ccafa2690b41475c5cc8')
        .then(response => response.data.bmx.series[0].datos)
    },
    splitUp(arr) {
      var newArray = [];
      var i = 0;
      var x = Math.ceil(arr.length / this.plazo)
      while (arr.length > 0) {
        newArray[i] = arr.splice(0, x)
        i++;
      }
      return newArray;
    },
    average(arr) {
      var avg = [];
      for (var i = 0; i < arr.length; i++) {
        var x = arr[i].reduce((a, b) => parseFloat(a) + parseFloat(b))
        avg[i] = x / arr[i].length
      }
      return avg
    },
    monthlyValues(arr) {
      var monthly = [];
      var cantidad = this.valor / parseFloat(arr[0])
      for (var i = 0; i < arr.length; i++) {
        monthly[i] = (-this.valor + cantidad * parseFloat(arr[i])).toFixed(3)
      }
      return monthly
    },
    chunkify(a, n, balanced) {

      if (n < 2)
        return [a];

      var len = a.length,
        out = [],
        i = 0,
        size;

      if (len % n === 0) {
        size = Math.floor(len / n);
        while (i < len) {
          out.push(a.slice(i, i += size));
        }
      } else if (balanced) {
        while (i < len) {
          size = Math.ceil((len - i) / n--);
          out.push(a.slice(i, i += size));
        }
      } else {

        n--;
        size = Math.floor(len / n);
        if (len % size === 0)
          size--;
        while (i < size * n) {
          out.push(a.slice(i, i += size));
        }
        out.push(a.slice(size * n));

      }

      return out;
    }
  }
}
</script>
