<template>
  <div>
    <div>
      <select class="form-control" v-model="year" @change="this.actualizarGrafico" name="años" id="" ref="years">
        <option selected value="2019">2019</option>
        <option value="2018">2018</option>
        <option value="2017">2017</option>
        <option value="2016">2016</option>
      </select>
    </div>

      <div class="container carousel">
        <div class="row">
          <div class="col">
            <swiper :options="swiperOption">
              <swiper-slide>
                <DolarChart class="graphs" ref="dolarchart" :year='year' />
              </swiper-slide>
              <swiper-slide>
                <CeteChart class="graphs" ref="cetechart" :year='year' />
              </swiper-slide>
              <div class="swiper-button-prev" slot="button-prev"></div>
              <div class="swiper-button-next" slot="button-next"></div>
            </swiper>
          </div>
        </div>
      </div>
  </div>
</template>

<style>
  @import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

  .container {
    margin-top: 2%;
  }

  .suma-dinamica {
    font-family: 'Lato', sans-serif;
  }

  .graphs {
    margin-bottom: 2%;
    padding-left: 3%;
    padding-right: 3%
  }

  #carousel {
    margin-top: 2%;
    margin-bottom: 15%;
  }
</style>

<script>
  //import TrazoChart from '@/components/TrazoChart'
  import DolarChart from '@/components/DolarChart'
  import CeteChart from '@/components/CeteChart'
  import 'swiper/dist/css/swiper.css'
  import {
    swiper,
    swiperSlide
  } from 'vue-awesome-swiper'

  export default {
    name: 'historico',
    data() {
      return {
        year: 2019,
        posts: null,
        arreglo: new Array(),
        swiperOption: {
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          }
        }
      }
    },
    components: {
      DolarChart,
      CeteChart,
      swiper,
      swiperSlide
    },
    methods: {
      actualizarGrafico() {
        this.$refs.dolarchart.updateData(this.year)
        this.$refs.cetechart.updateData(this.year)
      }
    },
    mounted() {
      this.actualizarGrafico()
    }
  }
</script>
