<template>
<line-chart :chart-data="datacollection" :options="options"></line-chart>
</template>

<script>
import LineChart from './LineChart.js'
import axios from 'axios'

export default {
  components: {
    LineChart
  },
  props:['year'],
  data() {
    return {
      arreglo:[],
      labels:[],
      datacollection: {},
      options: {
        scales: {
          yAxes: [{
            scaleLabel:{
            display:true,
            labelString:'MXN'
            },
            ticks: {
              stepSize:1,
              max:10,
              min:1
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [{
            scaleLabel:{
              display:true,
              labelString:'Semanas'
            },
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: true
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  methods: {
    fillData() {
      this.datacollection = {
        labels:this.labels,
        datasets: [{
          label: 'Cetes',
          backgroundColor: 'transparent',
          pointBackgroundColor: '#C49C48',
          borderWidth: 1,
          borderColor: '#C49C48',
          pointBorderColor: '#C49C48',
          data: this.arreglo
        }]
      }
    },
    datosAnuales(datos){
      var cierre = [];
      this.labels = [];
      for(var i = 0; i < datos.length; i++){
        cierre[i] = datos[i].dato;
        this.labels.push(i);
      }
      this.arreglo=cierre
      this.fillData()
    },
    updateData(year){
      this.year=year
      this.createData()
    },
    createData(){
      this.fetchData().then(res =>
        this.datosAnuales(res)
      )
    },
    fetchData(){
      return axios.get('https://www.banxico.org.mx/SieAPIRest/service/v1/series/SF60633/datos/' + this.year + '-01-01/' + this.year + '-12-31?token=478d9e956a835d3bd51f133a686556d79314083713b9ccafa2690b41475c5cc8')
        .then(response => response.data.bmx.series[0].datos)
    }
  }
}
</script>
