import Vue from 'vue'
import VueRouter from 'vue-router'
import Siitrazo from '../components/Siitrazo.vue'
import Historico from '../components/Historico.vue'
import Login from '../components/Login.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/siitrazo',
    name: 'siitrazo',
    component: Siitrazo
  },
  {
    path: '/historico',
    name: 'historico',
    component: Historico
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
