      <button class="btn">
        <i class="fas fa-arrow-left voxTitle"></i>
      </button>
    </router-link>
    <div class="col text-center">
      <img class="logo img-responsive center-block" src="../assets/logo-trazo.png" alt="logo trazo">
    </div>
  </div>


  <form @submit.prevent="actualizarGrafico(); sumaDinamica();">
    <div class="row">
      <div class="form-group col">
        <input type="text" placeholder="Plazo a Meses" name="" value="" class="form-control" v-model="plazo" required>
      </div>
