        <input type="text" placeholder="Plusvalía" name="" value="" class="form-control" v-model.number="plusvalia" required>
      </div>
      <div class="form-group col">
        <input type="text" placeholder="Inversión" name="" value="" class="form-control" v-model="valor" required>
      </div>
    </div>
    <div class="row">
      <div class="form-group col">
        <button class="btn btn-primary form-control" type="submit" name="button" @click="actualizarGrafico(); sumaDinamica(); actualizarAcumulados();">Cotizar</button>
      </div>
    </div>

  </form>
  <TrazoChart ref="trazochart" :valor="valor" />

  <div class="col banxico">
