    </div>
    <div class="col-3">
      <div class="vox">
        <p class="siete">{{prcTotal}}</p>
        <hr>
        <span class="full-text">Rendimiento + Plusvalía Total <span v-if="total!=0">
            {{total}}%</span></span>
        <span class="short-text"><span v-if="total!=0">
            {{total}}%</span></span>
      </div>
    </div>
