<script>
//import TrazoChart from '@/components/TrazoChart'
import TrazoChart from '@/components/TrazoChart'
//import Historico from '@/components/Historico'

export default {
  name: 'siitrazo',
  data() {
    return {
      valor: null,
      posts: null,
      plusvalia: null,
      plazo: null,
      rendimiento: null,
      prcRendimiento: null,
      prcPlusvalia: null,
      prcTotal: null,
      rMensual: null,
      acumuladoSii: null
    }
  },
  computed: {
    total: {
      get() {
        return Number(this.plusvalia) + Number(this.rendimiento)
      }
    }
  },
  components: {
    TrazoChart,
    //Historico
  },
  methods: {
    actualizarGrafico() {
      this.$refs.trazochart.updateData(this.valor, this.plazo, this.rendimiento, this.plusvalia)
    },
    sumaDinamica() {
      this.prcRendimiento = "$" + Math.round(this.valor * this.rendimiento * .01 / (12 / this.plazo)).toLocaleString()
      this.prcPlusvalia = "$" + Math.round(this.valor * this.plusvalia * .01 / (12 / this.plazo)).toLocaleString()
      this.prcTotal = "$" + Math.round(this.valor * (this.rendimiento + this.plusvalia) * .01 / (12 / this.plazo))
        .toLocaleString()
      this.rMensual = "$" + Math.round(this.valor * ((this.rendimiento + this.plusvalia) * .01 / 12)).toLocaleString()
    },
    actualizarAcumulados() {
      this.acumuladoSii = "$" + Math.round(this.valor * (this.rendimiento + this.plusvalia) * .01 / (12 / this.plazo))
        .toLocaleString()
    }
  }

}
</script>
