<template>
  <div class="container h-100 login">
    <div class="d-flex justify-content-center h-100 row">
      <div class="user_card animated fadeInDown">
        <div class="d-flex justify-content-center">
          <div class="brand_logo_container">
            <img src="../assets/logo-trazo.png" class="brand_logo" alt="Logo" />
          </div>
        </div>
        <div class="d-flex justify-content-center form_container">
          <form @submit.prevent="submit">
            <div class="input-group mb-3">
              <div class="input-group-append">
                <span class="input-group-text">
                  <i class="fas fa-user"></i>
                </span>
              </div>
              <input type="email" id="email" v-model="form.email" name = "email" class="form-control input_user" value placeholder="Email" />
            </div>
            <div class="input-group mb-2">
              <div class="input-group-append">
                <span class="input-group-text">
                  <i class="fas fa-key"></i>
                </span>
              </div>
              <input type="password" id="password" name="password" v-model="form.password" class="form-control input_pass" value placeholder="Contrasena" />
            </div>
            <div class="mt-4">
              <div class="d-flex justify-content-center links">
                <button type="submit" class="btn btn-primary">Entrar</button>
              </div>
            </div>
          </form>
        </div>


      </div>
    </div>
  </div>
</template>

<style>
  body,
  html {
    margin: 0;
    padding: 0;
    height: 100%;
  }

  .login {
    margin-top: 12%;
  }

  .brand_logo {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .user_card {
    height: 450px;
    width: 400px;
    margin-top: auto;
    margin-bottom: auto;
    background: #fff;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
  }

  .form_container {
    margin-top: 15%;
  }

  .login_btn {
    width: 100%;
    background: #000 !important;
    color: white !important;
  }

  .login_btn:focus {
    box-shadow: none !important;
    outline: 0px !important;
  }

  .login_container {
    padding: 0 2rem;
  }

  .input-group-text {
    background: #000 !important;
    color: white !important;
    border: 0 !important;
    border-radius: 0.25rem 0 0 0.25rem !important;
  }

  .input_user,
  .input_pass:focus {
    box-shadow: none !important;
    outline: 0px !important;
  }

  .custom-checkbox .custom-control-input:checked~.custom-control-label::before {
    background-color: #0066B3 !important;
  }
</style>

<script>
  import firebase from "firebase";
  export default {
    data() {
      return {
        form: {
          email: "",
          password: ""
        },
        error: null
      };
    },
    methods: {
      submit() {
        firebase
          .auth()
          .signInWithEmailAndPassword(this.form.email, this.form.password)
          .then(data => {
            this.$router.replace({
              name: "siitrazo"
            }, this.$toast.open('Login correcto'),
            console.log(data));
          })
          .catch(err => {
            this.$toast.open({
            message: "Email o contraseña invalido, intente de nuevo",
            type: "error"
          }, console.log(err))
          });
      }
    }
  };
</script>
